#home {
  @include transition(1s);
  width: 100%;
  position: absolute;

  top: 30%;
  color: $black;
  h1 {
    text-align: center;
    font-family: 'Play', Arial, sans-serif;
    text-shadow: -3px 3px 1px transparentize($black, .9);
    font-weight: normal;
    @media #{$brk-narrow} {
      font-size: 3em;
    }
  }
  h2 {
    text-align: center;
    font-size: Helvetica, Arial, sans-serif;
    font-weight: lighter;
    text-shadow: 1px 1px 3px transparentize($black, .9);
    @media #{$brk-narrow} {
      font-size: 2em;
    }
  }
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}
