.logo {
  @extend %transition-all;
  font-family: 'Play', Arial, sans-serif;
  font-size: 1.125em;
	padding: 10px;

	display: inline-block;
  color: transparentize($black, .5) !important;

  text-align: center;
  z-index: 5;

  @media #{$brk-wide} {
    font-size: 1.3em;

  }
}

.logo-opened {

  left: calc(65% - 44px);
  @media #{$brk-narrow} {
    left: calc(27% - 44px);
  }
  @media #{$brk-full-width} {
    left: calc(14% - 55px);
  }
}

.bold {
  font-weight: bold;
}

.active {
	@media #{$brk-narrow} {
		color: $white !important;

	}
}

.overflow-yes {
  width: 100%;
  font-weight: lighter;
  display: none;
  text-align: center;
  transform: rotate(-90deg) scale(1, 3) ;
}
