nav {
  @extend %transition-all;
  position: absolute;
  height: 50px;
  width: 100%;
  background: transparentize($white, .1);
  text-align: center;
  @media #{$brk-wide} {


  }
}



.nav-btn {
  width: 50px;
  height: 50px;
  position: absolute;
  left: 0;
  display: inline-block;
  text-align: left !important;
  z-index: 3;
  border-right: 1px solid transparentize($black, .8);
  .closed {
    width: auto;
    margin-top: 17px;
    @extend %transition-all;
    cursor: pointer;
    .nav-dash {
      height: 2px;
      width: 24px;
      margin: 5px auto ;
      background: transparentize($black, .5);
      position: relative;

    }
    .dash1 {
      @extend %transition-all;
      transform: rotate(0deg);
    }
    .dash2 {
      @extend %transition-all;
      transform: rotate(0deg);
    }
    .dash3 {
      @extend %transition-all;
      transform: rotate(0deg);
      opacity: 1;
    }
  }
  .opened {
    @extend %transition-all;
    width: auto;
    height: auto;
    margin-left: 10px;
    .nav-dash {

    }
    .dash1 {
      @extend %transition-all;
      transform: rotate(135deg);
      right: 5px;
      top: 5px;
    }
    .dash2 {
      @extend %transition-all;
      transform: rotate(-135deg);
      top: -1px;
      right: 5px;
    }
    .dash3 {
      @extend %transition-all;
      transform: rotate(135deg);
      opacity: 0;
    }
  }
  @media #{$brk-wide} {
    width: 65px;
  }
}

#nav-list {
  @include flexbox();
  @include transition(1s);
  background: $color-menu-bg;
  z-index: 1;
  -webkit-flex-direction: column;
  flex-direction: column;
  position: fixed;
  left: -100%;
  opacity: 0;
  top: 50px;
  width: 70%;
  height: calc(100vh - 50px);
  overflow: auto;
  clear: both;
  text-align: center;
  -webkit-align-items: stretch;
  align-items: stretch;
  -webkit-justify-content: center;
  justify-content: flex-start;
  box-shadow: inset -6px 6px 20px transparentize($black, .8),  -1px 1px 4px transparentize($black, .8);
  .shape-1 {
    margin-top: 80px;
    .line {
      position: relative;
      display: inline-block;
      width: 60px;
      height: 1px;
      background: $white;
      box-shadow: 1px 0 3px $black;
    }
    .box {
      position: relative;
      width: 7px;
      height: 7px;
      transform: rotate(45deg);
      display: inline-block;
      background: transparent;
      border: 1px solid $white;
      top: 3px;
      box-shadow: 1px 0 3px $black;
    }
  }
  .nav-item {
    @include flexbox();
    -webkit-align-items: stretch;
    align-items: stretch;
    text-align: center;
    margin: 20px;
    .nav-link {
      @extend %transition-all;
      padding: 17px 20px;
      flex-basis: 100%;
      text-align: center;
      text-transform: capitalize;
      text-decoration: none;
      text-shadow: 1px 1px 3px $black;
      color: $white;
      font-weight: lighter;
      font-size: 0.725em;
      .active-arrow {
        right: 10px;
        position: relative;
      }
    }
  }
  &:after {
    content: "";
    display: table;
    clear: both;
  }
  @media #{$brk-narrow} {
    width: 30%;
  }
  @media #{$brk-full-width} {
    width: 15%;
  }
}
